
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { WishlistListComponent } from './wishlist-list.component';
import { PaginationModule } from 'src/app/common/pagination/pagination.module';
import { LoderModule } from 'src/app/common/loder/loder.module';
import { NgSelectModule } from '@ng-select/ng-select';
import { FormsModule } from '@angular/forms';
import { WishlistService } from 'src/app/service/wishlist.service';

const routes: Routes = [
  {
    path: '',
    component: WishlistListComponent,
    // data: { title: 'company_list' }
  },
  {
    path: 'add',
    loadChildren: () => import('../wishlist-add/wishlist-add.module').then(m => m.WishlistAddModule),
    // data: { title: 'add_company' }
  },
  {
    path: 'edit/:id',
    loadChildren: () => import('../wishlist-add/wishlist-add.module').then(m => m.WishlistAddModule),
    // data: { title: 'edit_company' }
  },
];

@NgModule({
  declarations: [
    WishlistListComponent
  ],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    PaginationModule,
    LoderModule,
    NgSelectModule,
    FormsModule
  ],
  providers: [
    WishlistService
  ]
})

export class WishlistListModule { }
